import Testimonial from "../testimonial.json";

const testItemStyle = {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignContent: "center"
}
const containerStyle = {
    display:"flex",
    flexDirection:"row",
    justifyContent:"space-evenly",
    alignSelf:"center",
    flexWrap:"wrap",
    maxWidth:1000,
    margin: "auto"
}
const testNameStyle = {
    textAlign: "center",
    padding:10,
    fontSize:24,
}
const testStyleTest = {
    textAlign: "center",
    padding:4,
    fontSize:18,
}
const testTestStyle = {
    textAlign: "center",
    padding:4,
    fontSize:18,
}
const testDimStyle = {
    textAlign: "center",
    padding:4,
    fontSize:18,
}
const testImgStyle = {
    width:465,
}


function testimonialItem(test){
    var name = test.name;
    var testimonial = test.testimonial;
    return(
        <div style = {testItemStyle}>
<div style = {testTestStyle}>{testimonial}</div>
            <div style = {testNameStyle}>{name}</div>
            
            
        </div>
        
    );
}
export default function Testimonials(){
    return(
        <div style = {containerStyle}>
            {Testimonial.map(test=>testimonialItem(test))}
        </div>
    );
}